﻿.loader-container {
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    bottom: 0;
    background: #111;

    .loader-text {
        width: 100%;
        position: absolute;
        top: 47%;

        h1 {
            color: steelblue;
        }
    }
}


/*--Loader CSS--*/

.loader {
    position: relative;
    padding-top: 20%;
    width: 40px;
    margin: auto;


    .circle {
        position: absolute;
        width: 38px;
        height: 38px;
        opacity: 0;
        transform: rotate(225deg);
        animation-iteration-count: infinite;
        animation-name: orbit;
        animation-duration: 5.5s;

        &:after {
            content: '';
            position: absolute;
            width: 5px;
            height: 5px;
            border-radius: 5px;
            background: steelblue; /* Pick a color */
        }

        &:nth-child(2) {
            animation-delay: 240ms;
        }

        &:nth-child(3) {
            animation-delay: 480ms;
        }

        &:nth-child(4) {
            animation-delay: 720ms;
        }

        &:nth-child(5) {
            animation-delay: 960ms;
        }
    }
}

@keyframes orbit {
    0% {
        transform: rotate(225deg);
        opacity: 1;
        animation-timing-function: ease-out;
    }

    7% {
        transform: rotate(345deg);
        animation-timing-function: linear;
    }

    30% {
        transform: rotate(455deg);
        animation-timing-function: ease-in-out;
    }

    39% {
        transform: rotate(690deg);
        animation-timing-function: linear;
    }

    70% {
        transform: rotate(815deg);
        opacity: 1;
        animation-timing-function: ease-out;
    }

    75% {
        transform: rotate(945deg);
        animation-timing-function: ease-out;
    }

    76% {
        transform: rotate(945deg);
        opacity: 0;
    }

    100% {
        transform: rotate(945deg);
        opacity: 0;
    }
}
